<template>
    <div class="search_site">
        <!-- 头部开始 -->
        <header>
          <Head>
            <div slot='a' class="he login_he">
              <div class="left">
                <a-Icon type="left" @click='$router.go(-1)' style="line-height: 0.5rem" />
              </div>
              <div class="content" style="line-height: 0.5rem">搜索地址</div>
            </div>
          </Head>
        </header>
        <!-- 头部结束 -->
        <!-- 搜索开始 -->
        <div class="s_search">
          <div>
            <input type="text" placeholder="请输入小区/写字楼/学校等" v-model='name' />
            <input type="button" value="确认" @click='search()' />
          </div>
        </div>
        <!-- 搜索结束 -->
        <div class="hint">为了满足商家的送餐要求，建议您从列表中选择地址</div>

        <div v-if='show==false' style="width: 100%;text-align:center;margin-top: 1rem;font-size: 0.26rem;color: gray;line-height: 0.5rem">
            <div>找不到地址</div>
            <div>请尝试输入小区、写字楼或学校名</div>
            <div>详细地址(如门牌号)可稍后输入哦</div>
        </div>

        <router-link tag="ul" v-else v-for='(v,i) in searchsite' :to="'/Newly?name='+v.name+'&geohash='+v.geohash" :key="i">
            <li>{{v.name}}</li>
            <li>{{v.address}}</li>  
        </router-link>


    </div>
</template>

<script>
import Head from '@/page/header/Head' 
import axios from 'axios'   
export default{
    name:'Ssite',
    components:{Head},
    data() {
        return {
            name:'',
            searchsite:[],
            show:false
        }
    },
    methods: {
        search(){
            this.show = true
            var path = 'http://elm.cangdu.org/v1/pois?type=nearby&keyword='+this.name
            axios.get(path).then((res)=>{
                console.log(res)
                this.searchsite=res.data
            })
        }
    },
}
</script>

<style lang="scss">
.search_site{
    width: 100%;
    height: 100%;
    background: #F5F5F5;
    .s_search{
    width: 100%;
    height:0.8rem;
    background:white;
    text-align:center;
    line-height:0.8rem;
    font-size: 0.2rem;
    input[type='text']{
    width: 60%;
    height: 0.4rem;
    border: 1px solid gainsboro;
    background: #F5F5F5;
    border-radius: 0.05rem;
    box-sizing: border-box;
    padding: 0.05rem
    }
    input[type='button']{
    width: 15%;
    height: 0.4rem;
    background: #3190E8;
    color: white;
    text-align: center;
    line-height: 0.4rem;
    border-radius: 0.05rem;;
    }
    }
    .hint{
        width: 100%;
        height: 0.4rem;
        background: #FFF6E4;
        color: #FF883F;
        font-size: 0.2rem;
        text-align: center;
        line-height: 0.4rem;
    }
    ul{
        width: 100%;
        height: 0.8rem;
        background: #F5F5F5;
        border-bottom: 1px solid #CCCCCC;
        li{
            font-size:0.2rem;
            margin-top:0.1rem;
            padding-left:0.3rem;
        }
    }
}
</style>


